
import { defineComponent } from 'vue';
import Pagination from '@/shared/components/Pagination/Pagination.vue';
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import type {companyList,CIB20200211_REQ_BODY,CIB20200211_RES} from "@/shared/type/CIB20200211.type"
import { PAGE_SIZE } from "@/shared/constants/common.const";

export default defineComponent({
  name: 'cib202002sc00',
  components: {
    Pagination,
    BreadCrumb
  },
  data(){
    const companyList = [] as companyList[];
    const header = [
      { columnName: this.$t("cib202003sc00.no") },
      { columnName: this.$t("cib202003sc00.view") },
      { columnName: this.$t("cib202003sc00.customer_no") },
      { columnName: this.$t("cib202003sc00.customer_name") },
      { columnName: this.$t("cib202003sc00.master_user_id") },
      { columnName: this.$t("cib202003sc00.master_user_name") },
      { columnName: this.$t("cib202003sc00.cell_phone_no") },
      { columnName: this.$t("cib202003sc00.user_status") },
    ];
    return{
      searchForm:{
        customerNo:"",
        customerName:"",
        managerBankID:"",
        userID:""
      },
      header,
      companyList,
      pageNumber: 1,
			pageSize: PAGE_SIZE,
      isResetPage:false,
      startingNo:1,
      totalUser: 0,
      breadcrumbs: [
        { value: 'cib202002sc00.home', keyPath: '/COM200001SC00' , type: 'route'},
				{ value: 'cib202002sc00.ib_management', keyPath: '' , type: 'label'},
				{ value: 'cib202002sc00.sub_user_management', keyPath: '' , type: 'label'},
				{ value: 'cib202002sc00.company_list', keyPath: '' , type: 'label'},
			]
    }
  },

  mounted(){
    this.getCompanyUserList();
  },

  
  computed: {
    userStatusCode(){
      return (serviceStatusCode: string ,serviceStatusName: string) => `${serviceStatusCode}` + ' : ' + `${serviceStatusName}`;
    }
  },

  methods: {
    moveToSubUserList(userIndex:number){
      this.$router.push(
        {
          path: `CIB202003SC00`,
          query:{
            customerNo: this.companyList[userIndex].customerNo,
            managerBankID: this.companyList[userIndex].managerBankID,
            customerName: this.companyList[userIndex].customerName,
            cellPhoneNo: this.companyList[userIndex].cellPhoneNo,
            userID: this.companyList[userIndex].userID,
            companyName: this.companyList[userIndex].companyName,
            userStatus: this.companyList[userIndex].serviceStatusCode,
            mainUserRoleID: this.companyList[userIndex].mainUserRoleID
          }
        }
      )
    },

    async getCompanyUserList(mode?: string) {
      const reqBody: CIB20200211_REQ_BODY = {
        customerName: this.searchForm.customerName.trim(),
        customerNo: this.searchForm.customerNo.trim(),
        managerBankID: this.searchForm.managerBankID.trim(),
        pageSize: this.pageSize,
        pageNumber: mode == 'search' ? 0 : this.pageNumber
      };

      const resData = await this.$request.getRequest('CIB20200211', reqBody, false) as CIB20200211_RES;
      
      const userList = resData?.body?.list?.map((item: companyList, index: number) =>{
        item["No"] = index + this.startingNo;
        item["Customer No"] = item.customerNo;
        item["Customer Name"] = item.customerName;
        item["Master User ID"] = item.managerBankID;
        item["Master User Name"] = `${item.firstName +' '+ item.lastName}` 
        item["Cell Phone No"] = this.$format.formatPhoneNumber(item.cellPhoneNo);
        item["User Status"] = this.userStatusCode(item.serviceStatusCode, this.$codeConverter.convertValue("ServiceStatusCode", item.serviceStatusCode))
        return item;  
      });
  
      this.companyList = userList;
      this.totalUser = resData?.body?.totalCount;
		},

    handleSearch(mode: string){
      this.isResetPage = true;
      this.getCompanyUserList(mode);
    },

    handleResetSearch(){
      this.searchForm.customerNo = "";
      this.searchForm.customerName = "";
      this.searchForm.managerBankID = "";
      this.searchForm.userID = "";
      this.pageNumber = 1
      this.isResetPage = true;
      this.getCompanyUserList();
    },

    handlePagination(pageNumber: number){
      this.pageNumber = pageNumber;
      this.isResetPage = false;
      this.getCompanyUserList();
    },
  
    handleStartingIndex(startingIndex: number){
      this.startingNo = startingIndex;
    }
  }
})
